  stroke: #c0c0c0;
  stroke-dasharray: 10%;
  animation: dash 10s cubic-bezier(0.175, 0.885, 0.32, 1.275) infinite;
  stroke-width: 3px;
}

/* SIDE NAV */
.sidenav {
  display: inherit;
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  background-color: #2e2e2e;
  overflow-x: hidden;
  transition: 0.2s;
  padding-top: 6.64rem;
}
.sidenav li {
  list-style: none;
  text-align: center;
}
.sidenav a {
  padding: 1.6rem 1rem;
  text-decoration: none;
  font-size: 1.6rem;
  font-weight: 500;
  color: white;
  display: block;
  transition: 0.3s;
  mix-blend-mode: difference;
}
.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 1rem;
  font-size: 2.59rem;
}

/* Nav */
.nav {
  display: none;
}
.nav a {
  text-decoration: none;
  padding: 0.61rem 1rem;
  justify-self: end;
  margin-left: 1rem;
}
@media screen and (min-width: 766px) {
  .nav {
    mix-blend-mode: difference;
    display: inline-flex;
    position: fixed;
    list-style: none;
    transition: top 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
    margin: 0;
    padding-right: 2.59rem;
    text-align: center;
    height: 4.1rem;
    align-items: center;
    font-weight: 500;
    height: 6.64rem;
    top: inherit;
    right: 0;
  }
}

.nav-item a {
  text-decoration: none;
  color: #c0c0c0;
}

.nav-item-active a {
  color: black;
  background-color: white;
}

/* hambutton */
.hambutton {
  display: block;
  position: fixed;
  top: 1rem;
  left: 85vw;
  width: 2.59rem;
  mix-blend-mode: difference;
}
.hambutton:hover {
  cursor: pointer;
}
.hambutton line {
  stroke-width: 4px;
}
@media screen and (min-width: 766px) {
  .hambutton {
    display: none;
  }
}

